import React from "react";

import{NavLink} from "react-router-dom";

import{inject,observe} from "mobx-react";
//import CartState from "./cart/CartState";


@inject("cart")




export class Header extends React.Component{
    constructor(props){
super(props);
    }

//callback
//Before loading view
//called before render method
    componentWillMount(){
        console.log("hello console will mount");
    }

   //called afetr loading view
   //called after calling render method first time
   //view ready
    componentDidMount(){
        console.log("hello console did mount");
    }


    //called just before destroying componet
    componentWillUnmount(){
        console.log("hello console will Unmount");
    }

    

    render(){
        return( <div>
  hello header
  <h1>{this.props.title}</h1>

<NavLink to="/"
exact 
className="button"
activeClassName="success">
Home
</NavLink>

<NavLink to="/About"
exact 
className="button"
activeClassName="success">
About
</NavLink>


<NavLink to="/Contact"
exact 
className="button"
activeClassName="success">
Contact
</NavLink>

<NavLink to="/Cart"
exact 
className="button"
activeClassName="success">
Cart [{this.props.cart.cartSize}]
                        -RS {this.props.cart.amount}
</NavLink>

<NavLink to="/products"
exact 
className="button"
activeClassName="success">
Products
</NavLink>

    </div>
 )
}
}


